/**
 * How to apply for postgraduate taught degrees
 *
 * Don't run this via patterns.js, it's a separate compilation.
 *
 * @module patterns/how-to-apply/how-to-apply--pgt
 * @author Web Development
 * @copyright City, University of London 2020
 */

import React, { useEffect, useState } from 'react';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

import ApplyLinks from './buttons/apply';
import PropTypes from 'prop-types';
import createFocusTrap from 'focus-trap';

/**
 * Launch the How to apply modal.
 *
 * @param {object} props The JSON configuration file for the modal.
 * @return {object} The React component to render.
 */
function HowToApply(props) {
    let entryPoints = props.config;
    const [modalVisible, setModalVisible] = useState(false),
        [firstStep, setFirstStep] = useState(false), // Controls 'Start again' visibility
        [progressQualification, setProgressQualification] = useState(),
        [progressRoute, setProgressRoute] = useState(),
        [progressDate, setProgressDate] = useState(),
        [
            furtherStepsPendingIndicator,
            setFurtherStepsPendingIndicator,
        ] = useState(),
        [progressMethod, setProgressMethod] = useState(),
        [windowPrompt, setWindowPrompt] = useState(
            'Choose the qualification you wish to apply for:'
        ),
        promptQualification = 'Choose the qualification you wish to apply for:',
        promptRoute = 'Choose the route you wish to apply for:',
        promptEntryPoint = 'Choose the entry point you wish to apply for:',
        promptMethod = 'Apply online now:',
        furtherStepsPendingIndicatorMarkup = (
            <li className="how-to-apply--pgt--js__modal__progress__next">
                <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                    <span className="how-to-apply--pgt--js__modal__progress__circle">
                        <span
                            className="fas fa-circle icon"
                            aria-hidden="true"
                        ></span>
                    </span>
                </span>
            </li>
        );

    let furtherStepsPending,
        modalStepsCounter = 1,
        [
            selectionButtonQualification,
            setSelectionButtonQualification,
        ] = useState(),
        [
            selectionLinkQualification,
            setSelectionLinkQualification,
        ] = useState(),
        [selectionButtonRoute, setSelectionButtonRoute] = useState(),
        [selectionLinkRoute, setSelectionLinkRoute] = useState(),
        [selectionButtonDate, setSelectionButtonDate] = useState(),
        [selectionLinkDate, setSelectionLinkDate] = useState(),
        [selectionLinkMethod, setSelectionLinkMethod] = useState(),
        selectedQualificationData,
        selectedQualificationValue,
        selectedRouteData,
        selectedRouteValue,
        selectedDateData,
        selectedDateValue;

    /**
     * Clear any previously rendered selection buttons/links from the state to prevent superfluous button/link rendering
     */
    function clearSelectionOptions() {
        setSelectionButtonQualification();
        setSelectionLinkQualification();
        setSelectionButtonRoute();
        setSelectionLinkRoute();
        setSelectionButtonDate();
        setSelectionLinkDate();
        setSelectionLinkMethod();
    }

    /**
     * Convert raw date values to full month/year for rendering to buttons.
     *
     * @param {string} sourceDate The unformatted date string.
     */
    function formatDate(sourceDate) {
        const months = [
            'January',
            'February',
            'March',
            'April',
            'May',
            'June',
            'July',
            'August',
            'September',
            'October',
            'November',
            'December',
        ];
        let formattedDate = new Date(sourceDate);
        return `${
            months[formattedDate.getUTCMonth()]
        } ${formattedDate.getUTCFullYear()}`;
    }

    /**
     * Completed programme, route and dates filter. Analyse methods data.
     *
     * @param {object} data The filtered data record data, where methods are at the top level.
     */
    function filterMethodsData(methods) {
        // Methods of study pre-selection prompt text
        setWindowPrompt(promptMethod);

        // Clear any previously rendered selection buttons/links from the state to prevent superfluous button/link rendering
        clearSelectionOptions();

        // Variables scoped to this function
        let qualNav, routeNav, dateNav, methodNav;

        selectedQualificationData
            ? (qualNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              // Load qualification select; clear route, date and method from progress navigation
                              filterQualificationData(entryPoints);
                              setProgressRoute();
                              setProgressDate();
                              setProgressMethod();
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose qualification"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              Qualification
                          </span>
                      </button>
                  </li>
              ))
            : (qualNav = null);
        setProgressQualification(qualNav);

        selectedRouteData
            ? (routeNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              // Load route select; clear date and method from progress navigation
                              setProgressDate();
                              setProgressMethod();
                              if (selectedQualificationData) {
                                  filterRouteData(
                                      selectedQualificationData[0]['options']
                                  );
                              } else {
                                  filterQualificationData(entryPoints);
                              }
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose route"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              Route
                          </span>
                      </button>
                  </li>
              ))
            : (routeNav = null);
        setProgressRoute(routeNav);

        selectedDateData
            ? (dateNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              // Load date select; clear method from progress navigation
                              setProgressMethod();
                              if (selectedRouteData) {
                                  filterDatesData(
                                      selectedRouteData[0]['options']
                                  );
                              } else if (selectedQualificationData) {
                                  filterRouteData(
                                      selectedQualificationData[0]['options']
                                  );
                              } else {
                                  filterQualificationData(entryPoints);
                              }
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose route"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              Entry point
                          </span>
                      </button>
                  </li>
              ))
            : (dateNav = null);
        setProgressDate(dateNav);

        // Method loads apply links and, if present, will always be the last step, therefore progress icon does nothing on click
        methodNav = (
            <li className="how-to-apply--pgt--js__modal__progress__current">
                <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                    <span className="how-to-apply--pgt--js__modal__progress__circle">
                        <span className="fas fa-circle icon"></span>
                    </span>
                    <span className="how-to-apply--pgt--js__modal__progress__text">
                        Method of study
                    </span>
                </span>
            </li>
        );
        setProgressMethod(methodNav);

        // Final modal step; remove further steps pending indicator
        setFurtherStepsPendingIndicator();
        furtherStepsPending = false;

        // Calculate number of progress bar steps currently visible and set as data attribute on wrapper
        modalStepsCounter = 1;
        selectedQualificationData ? (modalStepsCounter += 1) : null;
        selectedRouteData ? (modalStepsCounter += 1) : null;
        selectedDateData ? (modalStepsCounter += 1) : null;
        furtherStepsPending ? (modalStepsCounter += 1) : null;

        // Modify JSX of progress bar wrapper to indicate no further steps are pending
        let wrapper = document.querySelector(
            '.how-to-apply--pgt--js__modal__progress'
        );
        wrapper.setAttribute('data-further-steps-pending', 'false');
        wrapper.setAttribute('data-total-steps', modalStepsCounter);

        let methodLinks = [];
        let linkOptions;

        methods.map((m) => {
            const methodLink = {
                text: m.header,
                link: m.options.apply,
            };
            methodLinks.push(methodLink);
            linkOptions = <ApplyLinks data={methodLinks} />;
            setSelectionLinkMethod(linkOptions);
        });
    }

    /**
     * Completed programme and route filter. Analyse dates data.
     *
     * @param {object} data The filtered data record data, where dates are at the top level.
     */
    function filterDatesData(data) {
        // Date pre-selection prompt text
        setWindowPrompt(promptEntryPoint);

        // Clear any previously rendered selection buttons/links from the state to prevent superfluous button/link rendering
        clearSelectionOptions();

        // Empty relevant state variables to remove superfluous progress navigation rendering
        setProgressMethod();

        // Clear data stored from previous selections
        selectedDateData = null;

        // Variables scoped to this function
        let buttonOptions,
            linkOptions,
            dateButtons = [],
            dateLinks = [],
            qualNav,
            routeNav,
            dateNav,
            dateLink;

        // Progress indicator; display selected qualification and route values
        selectedQualificationData
            ? (qualNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              filterQualificationData(entryPoints);
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose qualification"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              Qualification
                          </span>
                      </button>
                  </li>
              ))
            : (qualNav = null);
        setProgressQualification(qualNav);

        selectedRouteData
            ? (routeNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              filterRouteData(
                                  selectedQualificationData[0]['options']
                              );
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose route"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              Route
                          </span>
                      </button>
                  </li>
              ))
            : (routeNav = null);
        setProgressRoute(routeNav);

        // Method loads apply links and, if present, will always be the last step, therefore progress icon does nothing on click
        if (selectedDateData) {
            dateNav = (
                <li className="how-to-apply--pgt--js__modal__progress__current">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Entry point
                        </span>
                    </span>
                </li>
            );
        } else {
            dateNav = null;
        }
        setProgressDate(dateNav);

        data.map((d) => {
            let locations = d['options'];
            locations.map((l) => {
                let methods = l['options'];
                methods.map(() => {
                    // Multiple dates; print buttons
                    if (data.length > 1) {
                        // Date selection required: set progress indicator to route and remove methods
                        selectedDateValue = (
                            <li className="how-to-apply--pgt--js__modal__progress__current">
                                <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                                    <span className="how-to-apply--pgt--js__modal__progress__circle">
                                        <span className="fas fa-circle icon"></span>
                                    </span>
                                    <span className="how-to-apply--pgt--js__modal__progress__text">
                                        Entry point
                                    </span>
                                </span>
                            </li>
                        );
                        setProgressDate(selectedDateValue);
                        setProgressMethod();

                        // Single location, multiple dates
                        if (locations.length > 1 || methods.length > 1) {
                            // Single location, multiple dates, multiple methods => Dates as standard button; further options to follow
                            dateButtons.push(d.header);
                            dateButtons = dateButtons.reduce(function (a, i) {
                                a.indexOf(i) === -1 ? a.push(i) : null;
                                return a;
                            }, []);
                            buttonOptions = dateButtons.map((db) => {
                                return (
                                    <li key={db}>
                                        <button>
                                            <span
                                                data-date-src={db}
                                                onClick={(e) => {
                                                    let dateSrc = e.target.getAttribute(
                                                        'data-date-src'
                                                    );
                                                    selectedDateValue = formatDate(
                                                        dateSrc
                                                    );
                                                    selectedDateData = data.filter(
                                                        (fd) =>
                                                            fd.header ===
                                                            dateSrc
                                                    );
                                                    filterMethodsData(
                                                        selectedDateData[0][
                                                            'options'
                                                        ][0]['options']
                                                    );
                                                    setFirstStep(false);
                                                }}
                                            >
                                                {formatDate(db)}
                                            </span>
                                        </button>
                                    </li>
                                );
                            });

                            // Modal journey incomplete; display further steps pending indicator
                            setFurtherStepsPendingIndicator(
                                furtherStepsPendingIndicatorMarkup
                            );
                            furtherStepsPending = true;
                        } else {
                            // Single location, multiple dates, single method => Dates as links
                            dateLinks.push(d.header);
                            dateLinks = dateLinks.reduce(function (a, i) {
                                a.indexOf(i) === -1 ? a.push(i) : null;
                                return a;
                            }, []);

                            // Map each unique date and filter data to extract correct apply links
                            const dateLinksData = [];
                            dateLinks.map((lq) => {
                                let filteredDateData = data.filter(
                                    (q) => q.header === lq
                                );
                                dateLink = {
                                    text: formatDate(lq),
                                    link:
                                        filteredDateData[0]['options'][0][
                                            'options'
                                        ][0]['options']['apply'],
                                };
                                dateLinksData.push(dateLink);
                            });
                            linkOptions = <ApplyLinks data={dateLinksData} />;
                            // Final modal step; remove further steps indicator
                            setFurtherStepsPendingIndicator();
                            furtherStepsPending = false;
                        }
                    } else {
                        // Single date; move to methods function
                        filterMethodsData(data[0]['options'][0]['options']);
                    }
                });
            });
        });

        // Calculate number of progress bar steps currently visible and set as data attribute on wrapper
        modalStepsCounter = 1;
        selectedQualificationData ? (modalStepsCounter += 1) : null;
        selectedRouteData ? (modalStepsCounter += 1) : null;
        furtherStepsPending ? (modalStepsCounter += 1) : null;

        // Modify JSX of progress bar wrapper to indicate whether further steps are pending
        let wrapper = document.querySelector(
            '.how-to-apply--pgt--js__modal__progress'
        );
        furtherStepsPending
            ? wrapper.setAttribute('data-further-steps-pending', 'true')
            : wrapper.setAttribute('data-further-steps-pending', 'false');
        wrapper.setAttribute('data-total-steps', modalStepsCounter);

        // Update buttons/links state values (as required)
        linkOptions ? setSelectionLinkDate(linkOptions) : null;
        buttonOptions ? setSelectionButtonDate(buttonOptions) : null;
    }

    /**
     * Completed programme filter. Analyse routes data.
     *
     * @param {object} data The filtered data record data, where routes are at the top level.
     */
    function filterRouteData(data) {
        // Clear any previously rendered selection buttons/links from the state to prevent superfluous button/link rendering
        clearSelectionOptions();

        // Empty relevant state variables to remove superfluous progress navigation rendering
        setProgressDate();
        setProgressMethod();

        // Clear data stored from previous selections
        selectedRouteData = null;
        selectedDateData = null;

        // Variables scoped to this function
        let buttonQuals = [],
            linkRoutes = [],
            routeLink,
            buttonOptions,
            linkOptions,
            qualNav;

        // Progress indicator; display selected qualification value
        selectedQualificationData
            ? (qualNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              filterQualificationData(entryPoints);
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose qualification"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              Qualification
                          </span>
                      </button>
                  </li>
              ))
            : (qualNav = null);
        setProgressQualification(qualNav);

        data.map((s) => {
            // Routes pre-selection prompt text
            setWindowPrompt(promptRoute);
            let dates = s['options'];
            dates.map((d) => {
                let locations = d['options'];
                locations.map((l) => {
                    let methods = l['options'];
                    methods.map(() => {
                        if (data.length > 1) {
                            // Route selection required: set progress indicator to route and remove dates/methods
                            selectedRouteValue = (
                                <li className="how-to-apply--pgt--js__modal__progress__current">
                                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                                            <span className="fas fa-circle icon"></span>
                                        </span>
                                        <span className="how-to-apply--pgt--js__modal__progress__text">
                                            Route
                                        </span>
                                    </span>
                                </li>
                            );
                            setProgressRoute(selectedRouteValue);
                            setProgressDate();
                            setProgressMethod();

                            if (dates.length > 1 || methods.length > 1) {
                                // Standard route button
                                buttonQuals.push(s.header);
                                buttonQuals = buttonQuals.reduce(function (
                                    a,
                                    i
                                ) {
                                    a.indexOf(i) === -1 ? a.push(i) : null;
                                    return a;
                                },
                                []);
                                buttonOptions = buttonQuals.map((bq) => {
                                    return (
                                        <li key={bq}>
                                            <button>
                                                <span
                                                    onClick={(e) => {
                                                        selectedRouteValue =
                                                            e.target
                                                                .textContent;
                                                        selectedRouteData = data.filter(
                                                            (sr) =>
                                                                sr.header ===
                                                                selectedRouteValue
                                                        );
                                                        filterDatesData(
                                                            selectedRouteData[0][
                                                                'options'
                                                            ]
                                                        );
                                                        setFirstStep(false);
                                                    }}
                                                >
                                                    {bq}
                                                </span>
                                            </button>
                                        </li>
                                    );
                                });
                                // Modal journey incomplete; display further steps pending indicator
                                setFurtherStepsPendingIndicator(
                                    furtherStepsPendingIndicatorMarkup
                                );
                                furtherStepsPending = true;
                            } else {
                                // Route as link
                                linkRoutes.push(s.header);
                                linkRoutes = linkRoutes.reduce(function (a, i) {
                                    a.indexOf(i) === -1 ? a.push(i) : null;
                                    return a;
                                }, []);

                                // Map each unique route and filter data to extract correct apply links
                                const routeLinksData = [];
                                linkRoutes.map((lr) => {
                                    let filteredRouteData = data.filter(
                                        (q) => q.header === lr
                                    );
                                    routeLink = {
                                        text: lr,
                                        link:
                                            filteredRouteData[0]['options'][0][
                                                'options'
                                            ][0]['options'][0]['options'][
                                                'apply'
                                            ],
                                    };
                                    routeLinksData.push(routeLink);
                                });
                                linkOptions = (
                                    <ApplyLinks data={routeLinksData} />
                                );
                                // Final modal step; remove further steps indicator
                                setFurtherStepsPendingIndicator();
                                furtherStepsPending = false;
                            }
                        } else {
                            // Move to dates function
                            filterDatesData(data[0]['options']);
                        }
                    });
                });
            });
        });

        // Calculate number of progress bar steps currently visible and set as data attribute on wrapper
        modalStepsCounter = 1;
        selectedQualificationData ? (modalStepsCounter += 1) : null;
        furtherStepsPending ? (modalStepsCounter += 1) : null;

        // Modify JSX of progress bar wrapper to indicate whether further steps are pending
        let wrapper = document.querySelector(
            '.how-to-apply--pgt--js__modal__progress'
        );
        furtherStepsPending
            ? wrapper.setAttribute('data-further-steps-pending', 'true')
            : wrapper.setAttribute('data-further-steps-pending', 'false');
        wrapper.setAttribute('data-total-steps', modalStepsCounter);

        // Update buttons/links state values (as required)
        buttonOptions ? setSelectionButtonRoute(buttonOptions) : null;
        linkOptions ? setSelectionLinkRoute(linkOptions) : null;
    }

    /**
     * Analyse programme data record data.
     *
     * @param {object} data The original data record data, where programmes are at the top level.
     */
    function filterQualificationData(data) {
        // If qualification select exists, it will be the first step in the modal; disable 'Start again' option
        setFirstStep(true);

        // Qualification pre-selection prompt text
        setWindowPrompt(promptQualification);

        // Clear any previously rendered selection buttons/links from the state to prevent superfluous button/link rendering
        clearSelectionOptions();

        // Empty relevant state variables to remove superfluous progress navigation rendering
        setProgressRoute();
        setProgressDate();
        setProgressMethod();

        // Clear data stored from previous selections
        selectedQualificationData = null;
        selectedRouteData = null;
        selectedDateData = null;

        // Variables scoped to this function
        let buttonQuals = [],
            linkQuals = [],
            buttonOptions,
            linkOptions;

        if (data.length > 1) {
            // Qualification selection required: set progress indicator to qualification and remove routes/dates/methods
            selectedQualificationValue = (
                <li className="how-to-apply--pgt--js__modal__progress__current">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Qualification
                        </span>
                    </span>
                </li>
            );
            setProgressQualification(selectedQualificationValue);
        }

        data.length > 1
            ? data.map((e) => {
                  let routeNames = e['options'];
                  routeNames.map((s) => {
                      let dates = s['options'];
                      dates.map((d) => {
                          let locations = d['options'];
                          locations.map((l) => {
                              let methods = l['options'];
                              methods.map(() => {
                                  if (data.length > 1) {
                                      if (
                                          routeNames.length > 1 ||
                                          dates.length > 1 ||
                                          methods.length > 1
                                      ) {
                                          buttonQuals.push(e.header);
                                          buttonQuals = buttonQuals.reduce(
                                              function (a, i) {
                                                  a.indexOf(i) === -1
                                                      ? a.push(i)
                                                      : null;
                                                  return a;
                                              },
                                              []
                                          );
                                          buttonOptions = buttonQuals.map(
                                              (bq) => {
                                                  return (
                                                      <li key={bq}>
                                                          <button>
                                                              <span
                                                                  onClick={(
                                                                      e
                                                                  ) => {
                                                                      selectedQualificationValue =
                                                                          e
                                                                              .target
                                                                              .innerText;
                                                                      selectedQualificationData = data.filter(
                                                                          (
                                                                              qual
                                                                          ) =>
                                                                              qual.header ===
                                                                              selectedQualificationValue
                                                                      );
                                                                      filterRouteData(
                                                                          selectedQualificationData[0][
                                                                              'options'
                                                                          ]
                                                                      );
                                                                      setFirstStep(
                                                                          false
                                                                      );
                                                                  }}
                                                              >
                                                                  {bq}
                                                              </span>
                                                          </button>
                                                      </li>
                                                  );
                                              }
                                          );
                                          // Modal journey incomplete; display further steps pending indicator
                                          setFurtherStepsPendingIndicator(
                                              furtherStepsPendingIndicatorMarkup
                                          );
                                          furtherStepsPending = true;
                                      } else {
                                          linkQuals.push(e.header);
                                          linkQuals = linkQuals.reduce(
                                              function (a, i) {
                                                  a.indexOf(i) === -1
                                                      ? a.push(i)
                                                      : null;
                                                  return a;
                                              },
                                              []
                                          );

                                          // Map each unique qualification and filter data to extract correct apply links
                                          const methodLinks = [];
                                          linkQuals.map((lq) => {
                                              let filteredQualData = data.filter(
                                                  (q) => q.header === lq
                                              );
                                              const methodLink = {
                                                  text: lq,
                                                  link:
                                                      filteredQualData[0][
                                                          'options'
                                                      ][0]['options'][0][
                                                          'options'
                                                      ][0]['options'][0][
                                                          'options'
                                                      ]['apply'],
                                              };
                                              methodLinks.push(methodLink);
                                          });
                                          linkOptions = (
                                              <ApplyLinks data={methodLinks} />
                                          );
                                          // Move to end of function and use qualification variable? Similar approach to ternary operator used at end of dates function
                                      }
                                  }
                              });
                          });
                      });
                  });
              })
            : // One qualification; move to routes function
              filterRouteData(entryPoints[0]['options']);

        // Calculate number of progress bar steps currently visible and set as data attribute on wrapper
        modalStepsCounter = 1;
        furtherStepsPending ? (modalStepsCounter += 1) : null;

        // Modify JSX of progress bar wrapper to indicate whether further steps are pending
        let wrapper = document.querySelector(
            '.how-to-apply--pgt--js__modal__progress'
        );
        furtherStepsPending
            ? wrapper.setAttribute('data-further-steps-pending', 'true')
            : wrapper.setAttribute('data-further-steps-pending', 'false');
        wrapper.setAttribute('data-total-steps', modalStepsCounter);

        // Update buttons/links state values (as required)
        buttonOptions ? setSelectionButtonQualification(buttonOptions) : null;
        linkOptions ? setSelectionLinkQualification(linkOptions) : null;
    }

    // Modal wrapper render
    const question = 'qualification',
        qualificationQuestion = question === 'qualification' && (
            <>
                <div className="how-to-apply--pgt--js__modal__content-wrapper">
                    <p>{windowPrompt}</p>
                    <ul className="how-to-apply--pgt--js__options">
                        {selectionButtonQualification}
                        {selectionLinkQualification}
                        {selectionButtonRoute}
                        {selectionLinkRoute}
                        {selectionButtonDate}
                        {selectionLinkDate}
                        {selectionLinkMethod}
                    </ul>
                </div>
            </>
        ),
        alternative = props.element.dataset.otheryear &&
            props.element.dataset.otherurl && (
                <p className="how-to-apply--pgt--js__modal__alternative">
                    <a href={props.element.dataset.otherurl}>
                        {props.element.dataset.otheryear} entry is also
                        available.
                    </a>
                </p>
            );
    // End modal wrapper render

    // Focus trap
    const [focusTrap, setFocusTrap] = useState({});

    let modalRef = null,
        modalContentRef = null;
    const getModal = () => modalRef,
        getModalContent = () => modalContentRef;

    useEffect(() => {
        if (!focusTrap.activate) {
            setFocusTrap(
                createFocusTrap(getModalContent(), {
                    initialFocus: getModalContent().querySelector(
                        '.how-to-apply--pgt--js__options button, .how-to-apply--pgt--js__options a'
                    ),
                    onDeactivate: () => setModalVisible(false),
                    clickOutsideDeactivates: true,
                })
            );
        }

        if (modalVisible) {
            focusTrap.activate && focusTrap.activate();
            disableBodyScroll(getModal());
        } else {
            focusTrap.deactivate && focusTrap.deactivate();
            enableBodyScroll(getModal());
        }
    }, [modalVisible]);
    // End focus trap

    // Render the 'Apply now' CTA which launches the modal
    return (
        <>
            <ul className="cta-block">
                <li className="cta-block__cta">
                    <button
                        className="primary-cta-arrow--bright"
                        onClick={() => {
                            setModalVisible(true);
                            setFirstStep(true); // Don't show 'Start again' option on initial modal load
                            clearSelectionOptions(); // Clear any previously rendered selection buttons/links from the state
                            filterQualificationData(entryPoints);
                        }}
                    >
                        <span>Apply now</span>
                    </button>
                </li>
            </ul>
            <div
                className="how-to-apply--pgt--js__modal"
                data-open={modalVisible}
                ref={(modal) => (modalRef = modal)}
            >
                <div
                    className="how-to-apply--pgt--js__modal__content"
                    ref={(modalContent) => (modalContentRef = modalContent)}
                >
                    <button
                        className="how-to-apply--pgt--js__close"
                        onClick={() => {
                            focusTrap.deactivate && focusTrap.deactivate();
                            enableBodyScroll(getModal());
                            setModalVisible(false);
                        }}
                    >
                        <span
                            className="fas fa-times icon"
                            aria-label="Close"
                        ></span>
                    </button>

                    {/* Display 'Start again' reset if user is not in the modal's first selection screen */}
                    {!firstStep ? (
                        <div className="how-to-apply--pgt--js__modal__reset">
                            <button
                                onClick={() => {
                                    // Clear options display and render what user would see when modal is first opened
                                    setFirstStep(true);
                                    filterQualificationData(entryPoints);
                                }}
                            >
                                <span className="fas fa-redo"></span>{' '}
                                <span>Start again</span>
                            </button>
                        </div>
                    ) : null}
                    <ol className="how-to-apply--pgt--js__modal__progress">
                        {progressQualification}
                        {progressRoute}
                        {progressDate}
                        {progressMethod}
                        {furtherStepsPendingIndicator}
                    </ol>
                    <div className="how-to-apply--pgt--js__modal__content-wrapper">
                        <h2>Start in {props.element.dataset.year}</h2>
                    </div>
                    {qualificationQuestion}
                    <div className="how-to-apply--pgt--js__modal__content-wrapper">
                        {alternative}
                    </div>
                </div>
            </div>
        </>
    );
}

HowToApply.propTypes = {
    config: PropTypes.array,
    element: PropTypes.object,
};

export default HowToApply;
